import React from "react";
import { connect } from "react-redux";
import classnames from "classnames";
import { addProjectTask } from "../../../actions/backlogActions";
import { clearErrors } from "../../../actions/errorAction";
import PropTypes from "prop-types";
import { SingleDatePicker } from "react-dates";
import "../../Popup.scss";
import "../../Form.scss";

class AddTaskPopup extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      description: "",
      status: "",
      priority: 0,
      dueDate: null,
      projectIdentifier: this.props.project_tasks_id,
      errors: {},
    };
    this.onCancel = this.onCancel.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onCancel() {
    this.props.clearErrors();
    this.props.closePopup();
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();
    const newTask = {
      name: this.state.name,
      description: this.state.description,
      status: this.state.status,
      priority: this.state.priority,
      dueDate: this.state.dueDate,
    };
    this.props.addProjectTask(this.state.projectIdentifier, newTask);
  }

  inputOnFocus = (e) => {
    var el = document.getElementById(e.target.id).nextSibling;
    el.className = "customInputLabelActive";
  };

  inputOnBlur = (value, e) => {
    var el = document.getElementById(e.target.id).nextSibling;
    if (value.length === 0) {
      el.className = "customInputLabel";
    }
  };

  render() {
    const { errors } = this.state;
    return (
      <div className="popup">
        <div className="popupInner">
          <div className="taskPopUpTitle">Create New Task</div>
          <div className="container">
            <div className="row">
              <div className="col-md-8 m-auto">
                {
                  // form ===========================================================
                }
                <form onSubmit={this.onSubmit}>
                  {
                    // name ===========================================================
                  }
                  <div className="form-group">
                    <div className={errors.name ? "inputBoxLg" : "inputBox"}>
                      <input
                        id="name"
                        type="text"
                        className={classnames("customInput", {
                          "is-invalid": errors.name,
                        })}
                        name="name"
                        value={this.state.name}
                        onChange={this.onChange}
                        onFocus={this.inputOnFocus.bind(this)}
                        onBlur={this.inputOnBlur.bind(this, this.state.name)}
                      />
                      <label
                        className={
                          this.state.name
                            ? "customInputLabelActive"
                            : "customInputLabel"
                        }
                      >
                        Task Name
                      </label>
                      {errors.name && (
                        <div className="invalid-feedback">
                          <i class="fas fa-exclamation-circle"></i>
                          {errors.name}
                        </div>
                      )}
                    </div>
                  </div>
                  {
                    // description ===========================================================
                  }
                  <div
                    className="form-group"
                    style={
                      errors.description
                        ? { height: "6rem" }
                        : { height: "5rem" }
                    }
                  >
                    <div
                      className={errors.description ? "inputBoxLg" : "inputBox"}
                    >
                      <textarea
                        id="description"
                        className={classnames("customInput", {
                          "is-invalid": errors.description,
                        })}
                        name="description"
                        value={this.state.description}
                        onChange={this.onChange}
                        onFocus={this.inputOnFocus.bind(this)}
                        onBlur={this.inputOnBlur.bind(
                          this,
                          this.state.description
                        )}
                      />
                      <label
                        className={
                          this.state.description
                            ? "customInputLabelActive"
                            : "customInputLabel"
                        }
                      >
                        Description
                      </label>
                      {errors.description && (
                        <div className="invalid-feedback">
                          {errors.description}
                        </div>
                      )}
                    </div>
                  </div>
                  {
                    // Due Date ===========================================================
                  }
                  <div className="form-group">
                    <SingleDatePicker
                      date={this.state.dueDate} // momentPropTypes.momentObj or null
                      onDateChange={(date) =>
                        this.setState({ ...this.state, dueDate: date })
                      } // PropTypes.func.isRequired
                      focused={this.state.focused} // PropTypes.bool
                      onFocusChange={({ focused }) =>
                        this.setState({ focused })
                      } // PropTypes.func.isRequired
                      id="your_unique_id" // PropTypes.string.isRequired,
                      daySize={35}
                      openDirection="up"
                      placeholder="Due Date"
                    />
                  </div>
                  {
                    // Priority ===========================================================
                  }
                  <div className="form-group">
                    <div className="inputBox">
                      <select
                        id="priority"
                        className="customInput"
                        name="priority"
                        value={this.state.priority}
                        onChange={this.onChange}
                      >
                        <option value={0}>Select Priority</option>
                        <option value={1}>High</option>
                        <option value={2}>Medium</option>
                        <option value={3}>Low</option>
                      </select>
                    </div>
                  </div>
                  {
                    // status ===========================================================
                  }
                  <div className="form-group">
                    <div className="inputBox">
                      <select
                        className="customInput"
                        name="status"
                        value={this.state.status}
                        onChange={this.onChange}
                      >
                        <option value="">Select Status</option>
                        <option value="TO_DO">TO DO</option>
                        <option value="IN_PROGRESS">IN PROGRESS</option>
                        <option value="DONE">DONE</option>
                      </select>
                    </div>
                  </div>
                  {
                    // Button Group ===========================================================
                  }
                  <div className="row justify-content-between m-auto">
                    <input type="submit" className="btn col-5 submitBtn" />

                    <button
                      className="btn col-5 submitBtn"
                      onClick={this.onCancel}
                    >
                      Cancel
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AddTaskPopup.propTypes = {
  addProjectTask: PropTypes.func.isRequired,
  clearErrors: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  errors: state.errorReducer,
});

export default connect(mapStateToProps, { addProjectTask, clearErrors })(
  AddTaskPopup
);
