import React, { Component } from "react";
import { Link } from "react-router-dom";
import { deleteProjectTask } from "../../../actions/backlogActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";

import "./ProjectTask.scss";

class ProjectTask extends Component {
  constructor() {
    super();
  }

  // pass the id of the current task up to the projectboard component
  // then toggle the flag in projectboard to show the updatetaskpopup
  onUpdate(pt_id) {
    this.props.updateTaskID(pt_id);
    this.props.openPopup();
  }

  onDelete(backlog_id, pt_id, pt_name) {
    if (
      window.confirm(
        `Are you sure to delete the project task ${pt_name}? This action cannot be undone.`
      )
    ) {
      this.props.deleteProjectTask(backlog_id, pt_id);
    }
  }

  render() {
    const { project_task } = this.props;

    let priorityString;
    if (project_task.priority === 1) {
      priorityString = "HIGH";
    } else if (project_task.priority === 2) {
      priorityString = "MEDIUM";
    } else if (project_task.priority === 3) {
      priorityString = "LOW";
    }

    let statusID;
    if (project_task.status === "TO_DO") {
      statusID = "todo";
    } else if (project_task.status === "IN_PROGRESS") {
      statusID = "inprogress";
    } else {
      statusID = "done";
    }

    return (
      <div className="card mb-1">
        <div className="taskCard">
          <div className="additional" id={statusID}>
            <div className="user-taskCard">
              <div className="update" align="center">
                <a
                  className="buttonLink"
                  id={statusID}
                  onClick={this.onUpdate.bind(
                    this,
                    project_task.projectSequence
                  )}
                >
                  Update
                </a>
              </div>
              <div className="delete" align="center">
                <a
                  className="buttonLink"
                  id={statusID}
                  onClick={this.onDelete.bind(
                    this,
                    project_task.projectIdentifier,
                    project_task.projectSequence,
                    project_task.name
                  )}
                >
                  Delete
                </a>
              </div>
            </div>

            <div className="more-info">
              <h1>{project_task.name}</h1>

              <div className="moreInfoSubtitle">Description: </div>
              <div className="descriptionContent">
                {project_task.description}
              </div>

              <div className="coords">
                <span className="moreInfoSubtitle">Due Date:</span>
                <span>
                  {project_task.dueDate
                    ? project_task.dueDate.substring(0, 10)
                    : "Not Specified"}
                </span>
              </div>
              <div className="coords">
                <span className="moreInfoSubtitle">Created At:</span>
                <span>{project_task.create_At.substring(0, 10)}</span>
              </div>
              <div className="coords">
                <span className="moreInfoSubtitle">Updated At:</span>
                <span>
                  {project_task.create_At
                    ? project_task.create_At.substring(0, 10)
                    : "Not been updated"}
                </span>
              </div>
            </div>
          </div>

          <div className="general">
            <h1>{project_task.name}</h1>
            <div className="generalSubTitle">Description:</div>
            <div className="descriptionContent">{project_task.description}</div>
            <br />
            <div className="dueDate">
              <span className="generalSubTitle">Due Date: </span>
              <br />
              <span>
                {project_task.dueDate
                  ? " " + project_task.dueDate.substring(0, 10)
                  : " Not Specified"}
              </span>
            </div>
            <div className="priority">
              <span className="generalSubTitle">Priority: </span>
              <br />
              <span>{priorityString}</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

ProjectTask.propTypes = {
  deleteProjectTask: PropTypes.func.isRequired,
};

export default connect(null, { deleteProjectTask })(ProjectTask);
