import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { deleteProject } from "../../actions/projectActions";
import "./ProjectCard.scss";

class ProjectCard extends Component {
  constructor(props) {
    super(props);
    this.toggleClass = this.toggleClass.bind(this);
    this.state = {
      active: false,
    };
  }
  toggleClass() {
    const currentState = this.state.active;
    this.setState({ active: !currentState });
  }
  onDeleteClick = (id) => {
    this.props.deleteProject(id);
  };
  onUpdate() {
    this.props.openUpdatePopup();
    this.props.setUpdateID(this.props.project.projectIdentifier);
  }

  render() {
    const project = this.props.project;
    return (
      <div className="card mb-1" style={{ border: "none" }}>
        <div className="projectCard">
          <div className="bar"></div>

          <div className="general">
            <h1>{project.projectName}</h1>
            <div className="generalSubTitle">Description:</div>
            <div className="descriptionContent">{project.description}</div>
            <div className="projectID">
              <span className="generalSubTitle">
                Project ID: {project.projectIdentifier}
              </span>
            </div>
          </div>

          <div className="user-projectCard">
            <div className="projectBoard" align="center">
              <Link
                to={`/projectBoard/${project.projectIdentifier}`}
                className="buttonLink"
              >
                Project Board
              </Link>
            </div>
            <div className="update" align="center">
              <a className="buttonLink" onClick={this.onUpdate.bind(this)}>
                Update
              </a>
            </div>
            <div className="delete" align="center">
              <a
                className="buttonLink"
                onClick={this.onDeleteClick.bind(
                  this,
                  project.projectIdentifier
                )}
              >
                Delete
              </a>
            </div>
          </div>

          <button
            type="button"
            className="showDetail"
            onClick={this.toggleClass}
          >
            {this.state.active ? ">" : "<"}
          </button>

          <div className={this.state.active ? "base additional" : "base"}>
            {this.state.active ? (
              <div className="more-info">
                <h1>{project.projectName}</h1>

                <div className="moreInfoSubtitle">Description: </div>
                <div className="descriptionContent">{project.description}</div>

                <div className="row">
                  <div className="col-3">
                    <div className="moreInfoSubtitle">Start Date:</div>
                    <div className="moreInfoSubtitle">Estimated End Date:</div>
                  </div>
                  <div className="col-3 pl-0">
                    <div className="moreInfoSubtitle">
                      {project.start_date
                        ? project.start_date
                        : "Not Specified"}
                    </div>
                    <div className="moreInfoSubtitle">
                      {project.end_date ? project.end_date : "Not Specified"}
                    </div>
                  </div>

                  <div className="col">
                    <div className="moreInfoSubtitle">Project Created At:</div>
                    <div className="moreInfoSubtitle">Last Update At:</div>
                  </div>
                  <div className="col pl-0">
                    <div className="moreInfoSubtitle">
                      {project.created_At
                        ? project.created_At
                        : "Not Specified"}
                    </div>
                    <div className="moreInfoSubtitle">
                      {project.updated_At
                        ? project.updated_At
                        : "Not Been Updated"}
                    </div>
                  </div>
                </div>
              </div>
            ) : (
              <div style={{ height: "100%" }}></div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

ProjectCard.propTypes = {
  deleteProject: PropTypes.func.isRequired,
};

export default connect(null, { deleteProject })(ProjectCard);
