import React, { Component } from "react";
import { createNewUser } from "../../actions/securityActions";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";
import Header from "../Layout/Header";
import "./UserManagement.scss";

class Register extends Component {
  constructor() {
    super();
    this.state = {
      username: "",
      fullName: "",
      password: "",
      confirmPassword: "",
      errors: {},
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    if (this.props.security.validToken) {
      this.props.history.push("/dashboard");
    }
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();
    const newUser = {
      username: this.state.username,
      fullName: this.state.fullName,
      password: this.state.password,
      confirmPassword: this.state.confirmPassword,
    };
    this.props.createNewUser(newUser, this.props.history);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors,
      });
    }
  }

  inputOnFocus = (e) => {
    var el = document.getElementById(e.target.id).nextSibling;
    el.className = "customInputLabelActive";
  };

  inputOnBlur = (value, e) => {
    var el = document.getElementById(e.target.id).nextSibling;
    if (value.length === 0) {
      el.className = "customInputLabel";
    }
  };

  render() {
    const { errors } = this.state;
    return (
      <div>
        <Header />
        <div className="container" style={{ height: "530px" }}>
          <div className="row h-100">
            <div className="col-xs-10 col-sm-8 col-md-8 col-lg-6 m-auto">
              <div className="row" align="center">
                <div className="col">
                  <i className="fas fa-user-plus customIcon"></i>
                </div>
              </div>
              <div className="row" align="center">
                <div className="col customTitle">
                  <p>Sign Up</p>
                </div>
              </div>

              <div className="row">
                <div className="col">
                  <form onSubmit={this.onSubmit}>
                    <div className="form-group">
                      <div
                        className={errors.fullName ? "inputBoxLg" : "inputBox"}
                      >
                        <input
                          id="fullName"
                          type="text"
                          className={classnames("customInput", {
                            "is-invalid": errors.fullName,
                          })}
                          name="fullName"
                          value={this.state.fullName}
                          onChange={this.onChange}
                          onFocus={this.inputOnFocus.bind(this)}
                          onBlur={this.inputOnBlur.bind(
                            this,
                            this.state.fullName
                          )}
                          required
                        />
                        <label className="customInputLabel">Full Name</label>
                        {errors.fullName ? (
                          <div className="invalid-feedback">
                            {" "}
                            <i class="fas fa-exclamation-circle"></i>
                            {errors.fullName}{" "}
                          </div>
                        ) : (
                          <div></div>
                        )}
                      </div>
                    </div>

                    <div className="form-group">
                      <div
                        className={errors.username ? "inputBoxLg" : "inputBox"}
                      >
                        <input
                          id="username"
                          type="email"
                          className={classnames("customInput", {
                            "is-invalid": errors.username,
                          })}
                          name="username"
                          value={this.state.username}
                          onChange={this.onChange}
                          onFocus={this.inputOnFocus.bind(this)}
                          onBlur={this.inputOnBlur.bind(
                            this,
                            this.state.username
                          )}
                          required
                        />
                        <label className="customInputLabel">
                          Username (Email)
                        </label>
                        {errors.username ? (
                          <div className="invalid-feedback">
                            {" "}
                            <i class="fas fa-exclamation-circle"></i>
                            {errors.username}{" "}
                          </div>
                        ) : (
                          <div></div>
                        )}
                      </div>
                    </div>
                    <div className="form-group">
                      <div
                        className={errors.password ? "inputBoxLg" : "inputBox"}
                      >
                        <input
                          id="password"
                          type="password"
                          className={classnames("customInput", {
                            "is-invalid": errors.password,
                          })}
                          name="password"
                          value={this.state.password}
                          onChange={this.onChange}
                          onFocus={this.inputOnFocus.bind(this)}
                          onBlur={this.inputOnBlur.bind(
                            this,
                            this.state.password
                          )}
                          required
                        />
                        <label className="customInputLabel">Password</label>
                        {errors.password ? (
                          <div className="invalid-feedback">
                            {" "}
                            <i class="fas fa-exclamation-circle"></i>
                            {errors.password}{" "}
                          </div>
                        ) : (
                          <div></div>
                        )}
                      </div>
                    </div>
                    <div className="form-group">
                      <div
                        className={
                          errors.confirmPassword ? "inputBoxLg" : "inputBox"
                        }
                      >
                        <input
                          id="confirmpassword"
                          type="password"
                          className={classnames("customInput", {
                            "is-invalid": errors.confirmPassword,
                          })}
                          name="confirmPassword"
                          value={this.state.confirmPassword}
                          onChange={this.onChange}
                          onFocus={this.inputOnFocus.bind(this)}
                          onBlur={this.inputOnBlur.bind(
                            this,
                            this.state.confirmPassword
                          )}
                          required
                        />
                        <label className="customInputLabel">
                          Confirm Password
                        </label>
                        {errors.confirmPassword ? (
                          <div className="invalid-feedback">
                            {" "}
                            <i class="fas fa-exclamation-circle"></i>
                            {errors.confirmPassword}{" "}
                          </div>
                        ) : (
                          <div></div>
                        )}
                      </div>
                      {errors.confirmPassword && (
                        <div className="invalid-feedback">
                          {errors.confirmPassword}
                        </div>
                      )}
                    </div>
                    <input
                      type="submit"
                      className="btn btn-block mt-4 submitBtn"
                    />
                    <div className="row pt-2" align="center">
                      <div className="col" style={{ fontSize: "0.8rem" }}>
                        {" "}
                        Already Have an Account?{" "}
                        <Link to="/login"> Click Here to Log In</Link>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Register.protoTypes = {
  createNewUser: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
  security: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  errors: state.errorReducer,
  security: state.security,
});

export default connect(mapStateToProps, { createNewUser })(Register);
