import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createProject } from "../../actions/projectActions";
import { clearErrors } from "../../actions/errorAction";
import { DateRangePicker } from "react-dates";
import moment from "moment";
import classnames from "classnames";
import "../Popup.scss";

class AddProjectPopup extends Component {
  constructor() {
    super();
    this.state = {
      projectName: "",
      projectIdentifier: "",
      description: "",
      start_date: null,
      end_date: null,
      errors: {},
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onCancel = this.onCancel.bind(this);
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();
    const newProject = {
      projectName: this.state.projectName,
      projectIdentifier: this.state.projectIdentifier,
      description: this.state.description,
      start_date: this.state.start_date,
      end_date: this.state.end_date,
    };
    this.props.createProject(newProject, this.props.history);
  }

  onCancel() {
    this.props.closePopup();
    this.props.clearErrors();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  inputOnFocus = (e) => {
    var el = document.getElementById(e.target.id).nextSibling;
    el.className = "customInputLabelActive";
  };

  inputOnBlur = (value, e) => {
    var el = document.getElementById(e.target.id).nextSibling;
    if (value.length === 0) {
      el.className = "customInputLabel";
    }
  };

  render() {
    const { errors } = this.state;
    return (
      <div className="popup">
        <div className="popupInner">
          <div className="popUpTitle">Create New Project</div>
          <div className="container">
            <div className="row">
              <div className="col-md-8 m-auto">
                {
                  // form ======================================================================
                }

                <form onSubmit={this.onSubmit}>
                  {
                    // Project Name =====================================================================
                  }
                  <div className="form-group">
                    <div
                      className={errors.username ? "inputBoxLg" : "inputBox"}
                    >
                      <input
                        id="projectName"
                        type="text"
                        className={classnames("customInput", {
                          "is-invalid": errors.projectName,
                        })}
                        name="projectName"
                        required="required"
                        maxLength="30"
                        value={this.state.projectName}
                        onChange={this.onChange}
                        onFocus={this.inputOnFocus.bind(this)}
                        onBlur={this.inputOnBlur.bind(
                          this,
                          this.state.projectName
                        )}
                      />
                      <label className="customInputLabel">Project Name</label>
                      {errors.projectName && (
                        <div className="invalid-feedback">
                          <i class="fas fa-exclamation-circle"></i>
                          {errors.projectName}
                        </div>
                      )}
                    </div>
                  </div>
                  {
                    // Project Identifier =====================================================================
                  }
                  <div className="form-group">
                    <div
                      className={
                        errors.projectIdentifier ? "inputBoxLg" : "inputBox"
                      }
                    >
                      <input
                        id="projectIdentifier"
                        type="text"
                        className={classnames("customInput", {
                          "is-invalid": errors.projectIdentifier,
                        })}
                        name="projectIdentifier"
                        required="required"
                        value={this.state.projectIdentifier}
                        onChange={this.onChange}
                        onFocus={this.inputOnFocus.bind(this)}
                        onBlur={this.inputOnBlur.bind(
                          this,
                          this.state.projectIdentifier
                        )}
                      />
                      <label className="customInputLabel">
                        Project Identifier
                      </label>

                      {errors.projectIdentifier && (
                        <div className="invalid-feedback">
                          <i class="fas fa-exclamation-circle"></i>
                          {errors.projectIdentifier}
                        </div>
                      )}
                    </div>
                  </div>
                  {
                    // Description =====================================================================
                  }
                  <div
                    className="form-group"
                    style={
                      errors.description
                        ? { height: "6rem" }
                        : { height: "5rem" }
                    }
                  >
                    <div
                      className={errors.description ? "inputBoxLg" : "inputBox"}
                    >
                      <textarea
                        id="description"
                        className={classnames("customInput", {
                          "is-invalid": errors.description,
                        })}
                        name="description"
                        value={this.state.description}
                        onChange={this.onChange}
                        onFocus={this.inputOnFocus.bind(this)}
                        onBlur={this.inputOnBlur.bind(
                          this,
                          this.state.description
                        )}
                      />
                      <label className="customInputLabel">Description</label>

                      {errors.description && (
                        <div className="invalid-feedback-TA">
                          <i
                            class="fas fa-exclamation-circle"
                            style={{ marginRight: "0.5rem" }}
                          ></i>
                          {errors.description}
                        </div>
                      )}
                    </div>
                  </div>
                  {
                    // Start & End Date =====================================================================
                  }
                  <div className="form-group">
                    <DateRangePicker
                      startDate={this.state.start_date} // momentPropTypes.momentObj or null,
                      startDateId="your_unique_start_date_id" // PropTypes.string.isRequired,
                      endDate={this.state.end_date} // momentPropTypes.momentObj or null,
                      endDateId="your_unique_end_date_id" // PropTypes.string.isRequired,
                      onDatesChange={({ startDate, endDate }) =>
                        this.setState({
                          start_date: startDate,
                          end_date: endDate,
                        })
                      } // PropTypes.func.isRequired,
                      focusedInput={this.state.focusedInput} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                      onFocusChange={(focusedInput) =>
                        this.setState({ focusedInput })
                      } // PropTypes.func.isRequired,
                      openDirection="up"
                      isOutsideRange={() => false}
                      isDayHighlighted={(day) => day.isSame(moment(), "d")}
                    />
                  </div>
                  {
                    // Btn Group =====================================================================
                  }
                  <div className="row justify-content-between pt-1 m-auto">
                    <input
                      type="submit"
                      className="btn col-5 submitBtn"
                      type="submit"
                    />

                    <button
                      className="btn col-5 submitBtn"
                      onClick={this.onCancel}
                    >
                      Cancel
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AddProjectPopup.propTypes = {
  createProject: PropTypes.func.isRequired,
  clearErrors: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  errors: state.errorReducer,
});

export default connect(mapStateToProps, { createProject, clearErrors })(
  AddProjectPopup
);
