import React, { Component } from "react";
import Backlog from "./Backlog";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { getBacklog } from "../../actions/backlogActions";
import { getProject } from "../../actions/projectActions";
import { clearErrors } from "../../actions/errorAction";
import AddTaskPopup from "./ProjectTasks/AddTaskPopup";
import UpdateTaskPopup from "./ProjectTasks/UpdateTaskPopup";
import ProjectTitle from "./ProjectTitle";
import Header from "../Layout/Header";

class ProjectBoard extends Component {
  // constructor to handle errors
  constructor() {
    super();
    this.state = {
      errors: {},
      showCreatePopup: false,
      showUpdatePopup: false,
      update_task_id: "",
      start_date: null,
      end_date: null,
    };
    this.getUpdateTaskID = this.getUpdateTaskID.bind(this);
    this.toggleCreatePopup = this.toggleCreatePopup.bind(this);
    this.toggleUpdatePopup = this.toggleUpdatePopup.bind(this);
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getBacklog(id);
    this.props.getProject(id, this.props.history);
    this.props.clearErrors();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors,
      });
    }
  }

  toggleCreatePopup() {
    this.setState({
      showCreatePopup: !this.state.showCreatePopup,
    });
  }

  toggleUpdatePopup() {
    this.setState({
      showUpdatePopup: !this.state.showUpdatePopup,
    });
  }

  getUpdateTaskID = (value) => {
    this.setState({
      update_task_id: value,
    });
  };

  render() {
    const { id } = this.props.match.params;
    const { project_tasks } = this.props.backlog;
    const { errors } = this.state;

    let boardContent;
    const boardAlgorithm = (errors, project_tasks) => {
      // ==============================================================
      // the project is not found
      // ==============================================================
      if (errors.projectnotfound || errors.projectIdentifier) {
        return (
          <div className="alert alert-danger text-center" role="alert">
            The project {`${id}`} is not found in your account
          </div>
        );
      } else {
        // ==============================================================
        // the project exists
        // ==============================================================
        return (
          <div>
            {<ProjectTitle projectID={id} create={this.toggleCreatePopup} />}
            <hr style={{ minWidth: "300px" }} />
            {project_tasks.length === 0 ? (
              // ========================================================
              // the project has no task
              // ========================================================
              <div
                className="alert alert-info text-center"
                role="alert"
                style={{ margin: "0 1rem" }}
              >
                {` Let's add the first task of project "${this.props.project.projectName}"`}
              </div>
            ) : (
              // ========================================================
              // the project has some tasks
              // ========================================================
              <Backlog
                project_tasks_prop={project_tasks}
                updateTaskID={this.getUpdateTaskID}
                openPopup={this.toggleUpdatePopup}
              />
            )}
          </div>
        );
      }
    };
    boardContent = boardAlgorithm(errors, project_tasks);

    // pass a function to get task id to backlog, then backlog pass that function to projecttask
    // if update button is clicked in projecttask, that function is called to get the id of that task
    // the task id is then passed to updatetaskpopup to make an api call to get info of that task
    return (
      <div>
        <Header />
        <div className="container-fluid">
          {this.state.showCreatePopup ? (
            <AddTaskPopup
              closePopup={this.toggleCreatePopup}
              project_tasks_id={id}
            />
          ) : null}

          {this.state.showUpdatePopup ? (
            <UpdateTaskPopup
              closePopup={this.toggleUpdatePopup}
              backlog_id={id}
              pt_id={this.state.update_task_id}
            />
          ) : null}

          {boardContent}
        </div>
      </div>
    );
  }
}

ProjectBoard.protoTypes = {
  backlog: PropTypes.object.isRequired,
  getBacklog: PropTypes.func.isRequired,
  getProject: PropTypes.func.isRequired,
  clearErrors: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  project: state.projectReducer.project,
  backlog: state.backlog,
  errors: state.errorReducer,
});

export default connect(mapStateToProps, {
  getBacklog,
  getProject,
  clearErrors,
})(ProjectBoard);
