import React, { Component } from "react";
import ProjectTask from "./ProjectTasks/ProjectTask";
import "./ProjectBoardTheme.scss";
import "./Backlog.scss";

class Backlog extends Component {
  render() {
    const { project_tasks_prop } = this.props;
    const tasks = project_tasks_prop.map((project_task) => (
      <ProjectTask
        key={project_task.id}
        project_task={project_task}
        updateTaskID={this.props.updateTaskID}
        openPopup={this.props.openPopup}
      />
    ));

    let taskList = {
      TO_DO: [],
      IN_PROGRESS: [],
      DONE: [],
    };

    for (let i = 0; i < tasks.length; i++) {
      taskList[tasks[i].props.project_task.status].push(tasks[i]);
    }

    return (
      <div className="container-fluid">
        <div className="row">
          <div
            className="col-sm-12 col-md-6 col-lg-4"
            style={{
              minWidth: "300px",
            }}
          >
            <div className="card text-center mb-2">
              <div className="card-header text-white" id="todoCardTitle">
                <h3>To Do</h3>
              </div>
            </div>
            {taskList["TO_DO"]}
          </div>

          <div
            className="col-sm-12 col-md-6 col-lg-4"
            style={{
              minWidth: "300px",
            }}
          >
            <div className="card text-center mb-2">
              <div className="card-header text-white" id="inprogressCardTitle">
                <h3>In Progress</h3>
              </div>
            </div>
            {taskList["IN_PROGRESS"]}
          </div>

          <div
            className="col-sm-12 col-md-6 col-lg-4"
            style={{
              minWidth: "300px",
            }}
          >
            <div className="card text-center mb-2">
              <div className="card-header text-white" id="doneCardTitle">
                <h3>Done</h3>
              </div>
            </div>
            {taskList["DONE"]}
          </div>
        </div>
      </div>
    );
  }
}

export default Backlog;
