import React, { Component } from "react";
import { connect } from "react-redux";
import { getProjects } from "../actions/projectActions";
import PropTypes from "prop-types";
import ProjectCard from "./Project/ProjectCard";
import AddProjectPopup from "./Project/AddProjectPopup";
import UpdateProjectPopup from "./Project/UpdateProjectPopup";
import "./Dashboard.scss";
import Header from "./Layout/Header";

class Dashboard extends Component {
  constructor() {
    super();
    this.state = {
      showCreatePopup: false,
      showUpdatePopup: false,
      update_project_id: "",
    };
    this.setUpdateProjectID = this.setUpdateProjectID.bind(this);
    this.toggleCreatePopup = this.toggleCreatePopup.bind(this);
    this.toggleUpdatePopup = this.toggleUpdatePopup.bind(this);
  }

  toggleCreatePopup() {
    this.setState({
      showCreatePopup: !this.state.showCreatePopup,
    });
  }

  toggleUpdatePopup() {
    this.setState({
      showUpdatePopup: !this.state.showUpdatePopup,
    });
  }

  setUpdateProjectID = (value) => {
    this.setState({
      update_project_id: value,
    });
  };

  //life cycle hook
  componentDidMount() {
    this.props.getProjects();
  }

  render() {
    const { projects } = this.props.projects;
    return (
      <div className="projects">
        <Header />
        <div className="container-lg container-md container-sm-fluid">
          {this.state.showCreatePopup ? (
            <AddProjectPopup closePopup={this.toggleCreatePopup} />
          ) : null}

          {this.state.showUpdatePopup ? (
            <UpdateProjectPopup
              closePopup={this.toggleUpdatePopup}
              projectID={this.state.update_project_id}
            />
          ) : null}

          <div className="row">
            <div className="col-md-12">
              <h1 className="display-4 text-center">Projects</h1>
              <div className="row">
                <div className="col-lg-4 col-md-6 col-sm-12 col-xs-12">
                  <a className="landingBtn" onClick={this.toggleCreatePopup}>
                    <svg
                      className="bi bi-plus-square-fill"
                      width="2em"
                      height="1.2em"
                      viewBox="0 0 16 16"
                      fill="currentColor"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        fillRule="evenodd"
                        d="M2 0a2 2 0 00-2 2v12a2 2 0 002 2h12a2 2 0 002-2V2a2 2 0 00-2-2H2zm6.5 4a.5.5 0 00-1 0v3.5H4a.5.5 0 000 1h3.5V12a.5.5 0 001 0V8.5H12a.5.5 0 000-1H8.5V4z"
                        clipRule="evenodd"
                      />
                    </svg>
                    <span>Create a New Project</span>
                  </a>
                </div>
              </div>
              <hr />
              {projects.map((project) => (
                <ProjectCard
                  key={project.id}
                  project={project}
                  openUpdatePopup={this.toggleUpdatePopup}
                  setUpdateID={this.setUpdateProjectID.bind(this)}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Dashboard.propTypes = {
  projects: PropTypes.object.isRequired,
  getProjects: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  projects: state.projectReducer,
});

export default connect(mapStateToProps, { getProjects })(Dashboard);
