import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { logout } from "../../actions/securityActions";
import styles from "../../colors.scss";
import "./Header.scss";

class Header extends Component {
  handleLogout() {
    this.props.logout();
    window.location.href = "/";
  }

  render() {
    const { validToken, user } = this.props.security;

    const userIsNotAuthenticated = (
      <div className="collapse navbar-collapse" id="mobile-nav">
        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
            <Link className="nav-link" to="/register">
              Sign Up
            </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/login">
              Login
            </Link>
          </li>
        </ul>
      </div>
    );

    const userIsAuthenticated = (
      <div className="collapse navbar-collapse" id="mobile-nav">
        <ul className="navbar-nav mr-auto">
          <li className="nav-item">
            <Link className="nav-link" to="/dashboard">
              Dashboard
            </Link>
          </li>
        </ul>

        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
            <Link className="nav-link" to="/dashboard">
              <i className="fas fa-user-circle"></i>
              <span style={{ margin: "0 0 0 0.2rem" }}>{user.fullName}</span>
            </Link>
          </li>
          <li
            className="nav-item"
            style={{ display: "flex", justifyContent: "center" }}
          >
            <Link
              className="nav-link"
              to="/logout"
              style={{ alignSelf: "center" }}
              onClick={this.handleLogout.bind(this)}
            >
              <i className="fas fa-sign-out-alt"></i>
              <span style={{ margin: "0 0 0 0.2rem" }}>Logout</span>
            </Link>
          </li>
        </ul>
      </div>
    );

    let headerLinks;
    if (validToken && user) {
      headerLinks = userIsAuthenticated;
    } else {
      headerLinks = userIsNotAuthenticated;
    }

    return (
      <nav
        className="navbar navbar-expand-sm navbar-dark mb-4"
        style={{ backgroundColor: styles.navbar }}
      >
        <div className="container-md container-lg container-sm-fluid">
          <Link className="navbar-brand headerTitle" to="/">
            Smile Project
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#mobile-nav"
          >
            <span className="navbar-toggler-icon" />
          </button>

          {headerLinks}
        </div>
      </nav>
    );
  }
}

Header.protoTypes = {
  logout: PropTypes.func.isRequired,
  security: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  security: state.security,
});

export default connect(mapStateToProps, { logout })(Header);
