import axios from "axios";
import {
  GET_ERRORS,
  GET_BACKLOG,
  GET_PROJECT_TASK,
  DELETE_PROJECT_TASK,
  PROXY,
} from "./types";

export const addProjectTask = (backlog_id, project_task) => async (
  dispatch
) => {
  try {
    await axios.post(PROXY + `/api/backlog/${backlog_id}`, project_task);
    window.location.reload();
    dispatch({
      type: GET_ERRORS,
      payload: {},
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data,
    });
  }
};

export const updateProjectTask = (backlog_id, pt_id, pt) => async (
  dispatch
) => {
  try {
    await axios.post(PROXY + `/api/backlog/${backlog_id}/${pt_id}`, pt);
    window.location.reload();
    dispatch({
      type: GET_ERRORS,
      payload: {},
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data,
    });
  }
};

export const getBacklog = (backlog_id) => async (dispatch) => {
  try {
    const res = await axios.get(PROXY + `/api/backlog/${backlog_id}`);
    dispatch({
      type: GET_BACKLOG,
      payload: res.data,
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data,
    });
  }
};

export const getProjectTask = (backlog_id, pt_id, history) => async (
  dispatch
) => {
  try {
    const res = await axios.get(PROXY + `/api/backlog/${backlog_id}/${pt_id}`);
    dispatch({
      type: GET_PROJECT_TASK,
      payload: res.data,
    });
  } catch (err) {
    history.push("/");
  }
};

export const deleteProjectTask = (backlog_id, pt_id) => async (dispatch) => {
  await axios.delete(PROXY + `/api/backlog/${backlog_id}/${pt_id}`);
  dispatch({
    type: DELETE_PROJECT_TASK,
    payload: pt_id,
  });
};
