import axios from "axios";
import {
  GET_ERRORS,
  GET_PROJECTS,
  GET_PROJECT,
  DELETE_PROJECT,
  PROXY,
} from "./types";

export const createProject = (project) => async (dispatch) => {
  try {
    await axios.post(PROXY + "/api/project", project);

    dispatch({
      type: GET_ERRORS,
      payload: {},
    });
    window.location.reload();
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data,
    });
  }
};

export const updateProject = (project) => async (dispatch) => {
  try {
    const res = await axios.post(PROXY + "/api/project", project);
    dispatch({
      type: GET_PROJECT,
      payload: res.data,
    });
    dispatch({
      type: GET_ERRORS,
      payload: {},
    });
    window.location.reload();
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data,
    });
  }
};

export const getProjects = () => async (dispatch) => {
  const res = await axios.get(PROXY + "/api/project/all");
  dispatch({
    type: GET_PROJECTS,
    payload: res.data,
  });
};

export const getProject = (id, history) => async (dispatch) => {
  try {
    const res = await axios.get(PROXY + `/api/project/${id}`);
    dispatch({
      type: GET_PROJECT,
      payload: res.data,
    });
  } catch (error) {}
};

export const deleteProject = (id) => async (dispatch) => {
  if (window.confirm("Are you sure you want to delete this project?")) {
    await axios.delete(PROXY + `/api/project/${id}`);
    dispatch({
      type: DELETE_PROJECT,
      payload: id,
    });
  }
};
