import React, { Component } from "react";
import { login } from "../../actions/securityActions";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";
import Header from "../Layout/Header";
import "./UserManagement.scss";

class Login extends Component {
  constructor() {
    super();
    this.state = {
      username: "",
      password: "",
      errors: {},
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();
    const loginRequest = {
      username: this.state.username,
      password: this.state.password,
    };
    this.props.login(loginRequest);
  }

  componentDidMount() {
    if (this.props.security.validToken) {
      this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors,
      });
    }

    if (nextProps.security.validToken) {
      this.props.history.push("/dashboard");
    }
  }

  inputOnFocus = (e) => {
    var el = document.getElementById(e.target.id).nextSibling;
    el.className = "customInputLabelActive";
  };

  inputOnBlur = (value, e) => {
    var el = document.getElementById(e.target.id).nextSibling;
    if (value.length === 0) {
      el.className = "customInputLabel";
    }
  };

  render() {
    const { errors } = this.state;
    return (
      <div>
        <Header />
        <div className="container" style={{ height: "400px" }}>
          <div className="row h-100">
            <div className="col-xs-10 col-sm-8 col-md-8 col-lg-6 m-auto">
              <div className="row">
                <div className="col" align="center">
                  <i className="fas fa-unlock-alt customIcon"></i>
                </div>
              </div>
              <div className="row">
                <div className="col customTitle" align="center">
                  <p>Log in</p>
                </div>
              </div>
              <form onSubmit={this.onSubmit}>
                {
                  // username =========================================================================
                }
                <div className="form-group">
                  <div className={errors.username ? "inputBoxLg" : "inputBox"}>
                    <input
                      id="username"
                      type="email"
                      className={classnames("customInput", {
                        "is-invalid": errors.username,
                      })}
                      name="username"
                      value={this.state.username}
                      onChange={this.onChange}
                      onFocus={this.inputOnFocus.bind(this)}
                      onBlur={this.inputOnBlur.bind(this, this.state.username)}
                    />
                    <label className="customInputLabel">Username (Email)</label>
                    {errors.username && (
                      <div className="invalid-feedback">
                        <i class="fas fa-exclamation-circle"></i>
                        {errors.username}
                      </div>
                    )}
                  </div>
                </div>
                {
                  // password =========================================================================
                }
                <div className="form-group">
                  <div className={errors.password ? "inputBoxLg" : "inputBox"}>
                    <input
                      id="password"
                      type="password"
                      className={classnames("customInput", {
                        "is-invalid": errors.password,
                      })}
                      name="password"
                      value={this.state.password}
                      onChange={this.onChange}
                      onFocus={this.inputOnFocus.bind(this)}
                      onBlur={this.inputOnBlur.bind(this, this.state.password)}
                    />
                    <label className="customInputLabel">Password</label>
                    {errors.password ? (
                      <div className="invalid-feedback">
                        {" "}
                        <i class="fas fa-exclamation-circle"></i>
                        {errors.password}{" "}
                      </div>
                    ) : (
                      <div></div>
                    )}
                  </div>
                </div>
                {
                  // submit btn =========================================================================
                }
                <input type="submit" className="btn btn-block mt-4 submitBtn" />
                <div className="row pt-2" align="center">
                  <div className="col" style={{ fontSize: "0.8rem" }}>
                    {" "}
                    Don't Have an Account?{" "}
                    <Link to="/register"> Click Here to Creat an Account</Link>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Login.propTypes = {
  login: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
  security: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  security: state.security,
  errors: state.errorReducer,
});

export default connect(mapStateToProps, { login })(Login);
