import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import Background from "./Landing.jpg";
import PropTypes from "prop-types";
import "./Landing.scss";

class Landing extends Component {
  componentDidMount() {
    if (this.props.security.validToken) {
      this.props.history.push("/dashboard");
    }
  }

  render() {
    return (
      <div className="conatianer-fluid">
        <div className="row w-100 m-0">
          <div className="col-md-6 col-lg-8 p-0 blank">
            <div>
              <div className="row" align="center">
                <div className="col">
                  <div className="landingTitle">
                    <span>
                      Manage your amazing project through{" "}
                      <span class="webTitle">SmileProject</span>
                    </span>
                  </div>
                </div>
              </div>
              <div className="row" align="center">
                <div className="col">
                  <div className="landingDes">
                    This is the only project management app you will ever want
                    to use
                  </div>
                </div>
              </div>
              <div className="row pt-4 justify-content-between btnGroup">
                <div className="col-6">
                  <Link to="/login" className="landingBtn">
                    Login
                  </Link>
                </div>
                <div className="col-6">
                  <Link to="/register" className="landingBtn">
                    Sign Up
                  </Link>
                </div>
              </div>
            </div>
          </div>
          <div className="d-none d-md-block col-md-6 col-lg-4 p-0">
            <div className="landingImg"></div>
          </div>
        </div>
      </div>
    );
  }
}

Landing.propTypes = {
  security: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  security: state.security,
});

export default connect(mapStateToProps)(Landing);
